import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import HomeTitle from '../components/homepage/homeTitle'


import { graphql } from "gatsby"
import Img from 'gatsby-image'

const NotFoundPage = ({data}) => (
  <Layout>
    <SEO title="404: Not found" description="A página pretendida não existe. Certifique-se que introduziu o endereço correto." />
    <HomeTitle title="pagina não encontrada." leading="@pathfinderdesigns" />
    {/* <VideoCaption video={captionVideo} caption="A pagina pretendida não existe." /> */}
    <Img style={{height: '90vh'}} fluid={data.bgGatsbyImage.childImageSharp.fluid}></Img>
  </Layout>
)

export default NotFoundPage


export const query = graphql`
    query {
        bgGatsbyImage: file(relativePath: {eq: "404.jpg"}) {
            childImageSharp {
              fluid(maxWidth: 4000, quality: 100) {
                
                ...GatsbyImageSharpFluid_withWebp
                ...GatsbyImageSharpFluidLimitPresentationSize
              }
            }
        }
    }
`
